  .chart-wrapper .legend-btn {
    display: inline-block;
    line-height: 20px;
    font-size: 12px;
    margin-bottom: 0; }
    .chart-wrapper .legend-btn .legend-circle {
      display: inherit;
      height: 12px;
      width: 12px;
      border-radius: 3px;
      margin: 0 5px 5px 15px;
      box-sizing: border-box; }
    .chart-wrapper .legend-btn span {
      display: inline; }
    .chart-wrapper .legend-btn text {
      font-size: 12px;
      max-width: 100px;
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
      display: inherit; }
    .chart-wrapper .legend-btn.not-allowed {
      cursor: default; }
      .chart-wrapper .legend-btn.not-allowed .legend-circle:hover {
        cursor: default;
        box-shadow: none !important; }
  .chart-wrapper .legend-tooltip {
    padding: 0;
    border: none; }
    .chart-wrapper .legend-tooltip .legend-tooltip-header {
      color: white;
      font-size: 15px;
      font-weight: 700;
      padding: 13px 20px;
      margin: 0; }
    .chart-wrapper .legend-tooltip .legend-btn {
      display: inline-block;
      width: 100%; }
    .chart-wrapper .legend-tooltip text {
      max-width: 145px; }
    .chart-wrapper .legend-tooltip button {
      position: absolute;
      top: 7px;
      right: 10px; }
    .chart-wrapper .legend-tooltip .legend-tooltip-content {
      text-align: left;
      padding: 0 7px 15px 7px; }
  .chart-wrapper .right-legend {
    position: absolute;
    right: 0;
    overflow: auto;
    margin: 0;
    max-width: 30%;
    padding-left: 30px;
    white-space: inherit; }
    .chart-wrapper .right-legend text {
      padding-left: 15px; }
    .chart-wrapper .right-legend .legend-btn {
      width: 100%;
      text-indent: -.7em; }
      .chart-wrapper .right-legend .legend-btn text {
        max-width: 90%; }
    .chart-wrapper .right-legend .legend-circle {
      margin: 5px; }

div.chart-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.75); }
  div.chart-overlay span {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%); }
  div.chart-overlay div.loading-overlay-content {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%); }
    div.chart-overlay div.loading-overlay-content div.bx--loading {
      margin: auto; }

.tooltip {
  background-color: #fff;
  border: 1px solid #DFE3E6;
  padding: .25rem .5rem;
  display: block;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1);
  font-family: "IBM Plex Sans", Arial, sans-serif;
  font-size: 12px;
  font-weight: 300;
  position: absolute;
  padding: 10px;
  border-radius: 3px;
  max-width: 200px;
  word-wrap: break-word;
  z-index: 1059; }
  .tooltip p {
    margin: 0;
    font-size: 12px; }
    .tooltip p.bignum {
      font-size: 19px;
      font-weight: 600; }
  .tooltip b {
    font-weight: 600; }

#carbon-charts-patterns svg {
  height: 0; }
